.card {
  background-color: rgba(13, 17, 26, 0.631);
  padding: 5rem;
  border-radius: 2rem;
  max-width: 70rem;
  backdrop-filter: blur(0.5rem);
  /* margin-left: 20rem; */
  /* flex-grow: 0; */
  margin: 10rem 2rem 0 2rem;
  display: flex;
  justify-content: space-between;
  gap: 8rem;
  /* grid-template-columns: repeat(3, 1fr); */
}

@media screen and (max-width: 600px) {
  .card {
    max-width: 100%;
    margin: 0;
  }
}

.card-btn {
  background-color: red;
  display: none;
  /* BUG finish this button */
}

.card-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 0;
  max-width: 70rem;
}

h2 {
  font-size: 10rem;
  font-weight: 400;
}

h1 {
  font-size: 5.5rem;
  font-weight: 400;

  /* justify-self: center; */
}

p {
  font-size: 2rem;
}

